@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@500&family=Lato&display=swap);
body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    'Segoe UI',
    'Roboto',
    'Oxygen',
    'Ubuntu',
    'Cantarell',
    'Fira Sans',
    'Droid Sans',
    'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
}

code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    'Courier New',
    monospace;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  width: 50vw;
  min-width: 290px;
  font-size: calc(10px + 2vmin);
  border: 1px solid black;
  text-align: left;
  background: #ec4e8c;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: 'Lato', sans-serif;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.home {
  color: white;
  background: #803cb8;
  width: 100%;
  height: 100vh;
}

.home-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header-logo {
  width: 40%;
  font-size: 8rem;
  text-align: center;
  color: #c3afd6;
}

.header-text p {
  margin: 0;
}

.header-text {
  width: 50%;
  font-family: 'Cabin', sans-serif;
}

.header-app-name {
  font-size: 1.8rem;
  align-self: center;
  justify-self: center;
}

.header-country {
  font-size: 1.4rem;
}

/* styling cities */
.cities {
  width: 100%;
  margin: 0;
  padding: 0;
  background: #9497c0;
}

input::-webkit-input-placeholder {
  color: #fff;
}

input:-ms-input-placeholder {
  color: #fff;
}

input::placeholder {
  color: #fff;
}

.search-box {
  width: 100%;
  padding: 0.5rem 0 0.5rem 0;
  display: flex;
  align-items: center;
}

.search-icon {
  margin-left: 1rem;
}

.search-city {
  margin-left: 0.2rem;
  background: none;
  width: 90%;
  border: none;
  outline: none;
  color: white;
  font-size: 1.2rem;
}

a {
  text-decoration: none;
  color: white;
  justify-content: center;
  margin: 0.5rem;
}

.city-list {
  width: 100%;
  margin: 0;
  padding: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.city-list a:nth-child(4n-1) {
  background: #803cb8;
}

.city-list a:nth-child(4n-2) {
  background: #803cb8;
}

.card-country {
  display: flex;
  justify-content: space-between;
  align-content: center;
  flex-direction: column;
  width: 45%;
  height: 10rem;
  background-color: #803cb8;
  padding-right: 0.5rem;
  padding-top: 0.5rem;
}

/* Scale up the ".box" */
.card-country:hover {
  -webkit-transform: scale(1.03, 1.03);
          transform: scale(1.03, 1.03);
  transition: opacity 0.3s ease-in-out;
}

.card-country p {
  margin: 0;
}

.detail-btn {
  align-self: end;
}

.country-name {
  align-self: center;
  justify-self: center;
  font-size: 1.3rem;
  color: white;
  font-family: 'Cabin', sans-serif;
}

.country-desc {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  width: 50%;
  height: 100%;
  font-size: 1rem;
}

.flag-country {
  align-self: flex-end;
  display: inline-flex;
  height: 1.5rem;
}

.img-flag {
  height: 16px;
  width: 16px;
  display: flex;
  align-items: flex-end;
  margin-right: 0.2rem;
}

.img-flag img {
  width: 100%;
}

.country-capital {
  margin: 0;
  padding: 0;
  font-size: 1rem;
}

@media (max-width: 960px) {
  .card-country {
    width: 42%;
  }

  .header-app-name {
    font-size: 1.6rem;
  }

  .header-logo {
    font-size: 6rem;
  }

  .search-city,
  .country-name {
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  .card-country {
    width: 40%;
  }

  .header-app-name {
    font-size: 1.5rem;
  }
}

.weather-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

/* card weather list icons and text */
.weather-list li {
  display: flex;
  align-items: center;
  padding: 0.2rem;
}

.sun-display p {
  font-size: 1.2rem;
}

.weather-list li p {
  margin: 0 0 0 0.2rem;
  font-size: 1.1rem;
}

.card-icon {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
}

/* Sunrise and sunset group */
.sun-display {
  display: flex;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin-top: 1rem;
}

.sunrise,
.sunset {
  width: 50%;
  text-align: center;
  padding: 0.5rem 0 0.5rem 0;
  margin-bottom: 2px;
}

.sunrise {
  background: #f35bc579;
}

.sunset {
  background: #8d2c53;
}

.sunrise-text {
  margin: 0;
}

.sun-display-icon {
  margin: 0;
  font-size: 2rem;
}

p {
  margin: 0;
}

.detail-pg {
  color: white;
  width: 100%;
  background: rgb(212, 71, 128);
  background: linear-gradient(190deg, rgba(212, 71, 128, 1) 16%, rgba(158, 49, 93, 1) 38%, rgba(31, 29, 54, 1) 82%);
}

.page {
  margin: 0 1rem 0 1rem;
}

.main-time h1,
h2 {
  margin: 0;
  font-family: 'Cabin', sans-serif;
}

.region {
  font-size: 2.3rem;
  padding: 1rem 0 1rem 0;
}

.date {
  font-size: 1.3rem;
}

.desc-icon {
  padding: 2rem 0 1rem 0;
}

.weather-descs {
  font-size: 1.2rem;
  text-align: center;
  font-style: italic;
  -webkit-animation: myAnim 1s ease 0s 1 normal forwards;
          animation: myAnim 1s ease 0s 1 normal forwards;
}

@-webkit-keyframes myAnim {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-250px);
            transform: translateX(-250px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

@keyframes myAnim {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-250px);
            transform: translateX(-250px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
}

.weather-icon {
  text-align: center;
}

/* weekly update */

.weekly-update-title {
  margin: 2.4rem 0 0.4rem 0;
  font-family: 'Cabin', sans-serif;
}

.weeks {
  margin: 0;
  padding: 0;
  width: 100%;
}

.weeks li {
  width: 100%;
  list-style: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0 0.5rem 0;
  font-size: 1.13rem;
}

.weekly-group {
  display: flex;
  align-items: center;
  margin: 0;
  flex: 1 1;
}

.weekly-temp {
  flex: 1 1;
  text-align: center;
}

.day {
  flex: 1 1;
  text-align: center;
  padding-right: 0.5rem;
  margin: 0;
}

.weekly-desc {
  padding-right: 0.5rem;
}

.weeks li:nth-child(even) {
  background: #9e315d;
}

.navbar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 2rem;
  padding: 0.5rem 0 0.5rem 0;
  background: #9497c0;
}

.arrowback {
  color: white;
}

.nav-tools {
  color: white;
  display: flex;
  padding-right: 0.4rem;
}

.microphone {
  padding-right: 0.5rem;
}

